@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap');

*{
  margin: 0;
  padding: 0;
}
body{
  background: #fff;
  color: #222;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
}
.wrapper{
  width: 1280px;
  margin: 50px auto;
}

header .logo{
  font-weight: 600;
  font-size: 20px;
}

header ul.nav{
  float: right;
  list-style: none;
}

header ul.nav li{
  display: inline-block;
  margin-left: 25px;
  cursor: pointer;
  transition: 2s ease-in;
}

header ul.nav li:hover{
  text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #e60073, 0 0 40px #e60073, 0 0 50px #e60073, 0 0 60px #e60073, 0 0 70px #e60073;
  transition: 2s ease-in;
}

header .presentation{
  margin: 50px 0;
  background: url("./img/backgorund.jpg") no-repeat;
  width: 100%;
  height: 500px;
  background-size: cover;
  background-position: center center;
  background-blend-mode: multiply;
  background-color: #c581d5;
  position: relative;
}

header .presentation::after{
  content: "Лучшие товары для Вашего дома";
  position: absolute;
  top: 100px;
  left: 50px;
  width: 300px;
  font-size: 40px;
  font-weight: 600;
  color: white;
}
header .presentation::before{
  content: "по низким ценам";
  position: absolute;
  top: 250px;
  left: 50px;
  width: 300px;
  font-size: 15px;
  font-weight: 600;
  color: white;
}
footer{
  text-align: center;
  margin-top: 10px;
}